import Vue from 'vue';
import Router from 'vue-router';
import {async_load} from '../assets/async_load';
const CrowdFund = async_load(() => import('@/components/myIndex/CrowdFund'))
const MyData = async_load(() => import('@/components/myData/myData'))
// import CrowdFund from '@/components/myIndex/CrowdFund';
// import MyData from '@/components/myData/myData';
Vue.use(Router);
const router = new Router({
  routes: [
    {
      path: '/',
      name: 'CrowdFund',
      component: CrowdFund
    },
    {
      path: '/CrowdFund',
      name: 'CrowdFund',
      component: CrowdFund
    },
    {
      path: '/MyData',
      name: 'MyData',
      component: MyData,
    }
  ]
});
// router.beforeEach((to, from, next) => {
//   // if(to.name == '/MyData'){
//   //   next({path:'/MyData'})
//   // }
//   console.log(to.name)
//   // if(!to.name){
//   //   next(true)
//   // }
// })

export default router;




