import Vue from 'vue'
import App from './App'
import router from './router'
import * as Ether from "ethereumjs-util";
Vue.config.productionTip = false;
Vue.prototype.$Ether = Ether;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});



